














































import Vue from 'vue'
import duty from './Duty.vue'
export default Vue.extend({
  name: 'dutyPortal',
  components: { duty },
  data () {
    return {
      step: 0,
      showDuty: false
    }
  },
  methods: {
    showQuestion () {
      if (this.step > 5) return
      setTimeout(() => {
        this.step++
        this.showQuestion()
      }, 200)
    },
    handleClick () {
      if (this.step === 0) {
        this.showQuestion()
        return
      }
      if (this.step > 6) {
        this.showDuty = true
        return
      }
      this.step++
    }
  }
})
